*{
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 650px;
  background-color: #118ab2;
  
}

#quote-box {
  border: 2px solid #f78c6b;
  width: 50vw;
  height: fit-content;
  background-color: #f78c6b;
  position: relative;
  border-radius: 30px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2), 0 20px 40px rgba(0, 0, 0, 0.2);
}

h1 {
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: clamp(10px, 4vw, 35px);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  color: #27213C;
}

#text {
  width: 80%;
  min-height: 100px;
  height: fit-content;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2), 0 20px 40px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 5px;
  font-size: 18px;
  color: #27213C;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

#author {
  width: fit-content;
  color: black;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2), 0 20px 40px rgba(0, 0, 0, 0.2);
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  margin-left: auto;
}

#author i {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.fade-out {
  opacity: 0;
}

.fade-in {
  opacity: 1;
}

#new-quote {
  position: relative;
  height: 40px;
  border-radius: 15px;
  background-color: #f78c6b; /* Pink base color */
  background-image: radial-gradient(circle at 50% 50%, #ffffff, #f78c6b 60%, #f78c6b);
  border: 1px solid black;
  border-radius: 12px;
  box-shadow: inset 0 -2px 4px rgba(255, 255, 255, 0.5), inset 0 2px 4px rgba(0, 0, 0, 0.2), 0 4px 8px rgba(0, 0, 0, 0.3);
  color: black;
  width: clamp(50px, 9vw, 90px); 
  padding: 5px;
  margin-right: 10px;
}
#new-quote:hover {
  top: -2px;
  background-image: radial-gradient(circle at 50% 50%, #f78c6b, white 60%, white);
}

#new-quote:active {
  top: 0px;
}

img {
  width: 40px;
}
img:hover {
  position: relative;
  top: -2px;
}

a {
  width: 40px;
  height: 40px;
}

#innerWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 57px;
  padding-right: 57px;
  margin-top: 14px;
  margin-bottom: 10px;
}

#imageDiv {
  height: fit-content;
  width: fit-content;
}

#flexItem {
  display: flex;
  flex-direction: row;
}



